.aberrusNav {
    background-color    : var(--hc-color-aberrus-bg-2);

    height              : 50px;
    padding             : 10px;

    display             : flex;
    align-items         : center;
}
.aberrusNav .bossIcon {
    border              : 2px solid var(--hc-color-point-1);
    border-radius       : 50%;

    width               : 46px;
    height              : 46px;

    margin-right        : 10px;
}
/* -------------------------------------------------- */
.aberrusTitle {
    background-color    : var(--hc-color-aberrus-bg-1);

    height              : 150px;
    
    padding-left        : 20px;

    display             : grid;
    grid-template-columns: 1fr 1fr;
}
.aberrusTitleText {
    display             : flex;
    flex-direction      : column;
    justify-content     : center;
}
.aberrusTitleText .dungeon {
    color               : var(--hc-color-point-1);

    font-size           : 1rem;
}
.aberrusTitleText .name {
    font-family         : var(--hc-font-strong);
    font-size           : 2.5rem;
}
.aberrusTitle .img {
    width               : 200px;

    display             : flex;
    align-items         : flex-end;
    margin-left         : auto;
}
/* -------------------------------------------------- */
.aberrusDifficulty {
    background-color    : var(--hc-color-aberrus-bg-2);

    height              : 50px;

    display             : grid;
    grid-template-columns: 1fr 1fr;
}
.aberrusDifficultyText {
    font-size           : 1.2rem;

    display             : flex;
    justify-content     : center;
    align-items         : center;
}
/* -------------------------------------------------- */
.aberrusBar {
    background-color    : var(--hc-color-aberrus-bg-1);

    height              : 50px;
    padding             : 0px 20px 0px 20px;

    display             : flex;
    align-items         : center;

    font-family         : 'Black Han Sans', sans-serif;
    font-size           : 1.4rem;
}
/* -------------------------------------------------- */
.aberrusBarInsert {
    background-color    : var(--hc-color-aberrus-bg-2);

    padding             : 20px 20px 20px 20px;
}
/* -------------------------------------------------- */
.mechanic {
    margin-bottom       : 20px;
}
.mechanicTitle {
    background-color    : var(--hc-color-aberrus-bg-1);
    border-bottom       : 1px solid black;
    padding             : 10px;
    display             : flex;
    align-items         : center;
}
.mechanicTitle .img {
    width: 50px;
    border: 3px solid #5b5b5b;
    border-radius: 10px;
    margin-right: 10px;
}
.mechanicTitle .name {

}
.mechanicDescription {
    background-color: var(--hc-color-aberrus-bg-1);
    padding: 20px;
    padding-top: 10px;
}
.MechanicSub {
    background-color: var(--hc-color-aberrus-bg-2);
    height: 30px;
    padding: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.MechanicSubIcon {
    border: 2.5px solid #5b5b5b;
    width: 30px;
    border-radius: 7px;
    margin-right: 10px;
}

/* -------------------------------------------------- */

.textBarInsertPhase {
    background-color: var(--hc-color-aberrus-bg-1);
    width: 150px;
    height: 30px;
    padding: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}


















